
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import SelectDown from '../../components/SelectDown/selectdown.vue';
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
components: {
PopupPicker,
XHeader,
Toast,
SelectDown
},
data() {
return {
detailsUrl: 'hospitalInfo1',
showSelect1: false,
showPopupPicker1: false,
sVal1: {},
popupVal1: [],
noData: false,
selectList1: [{
val: '三甲',
key: '1'
}, {
val: '专科',
key: '2'
}, {
val: '综合',
key: '3'
}, {
val: '全部',
key: '4'
}],
list: [],
isloading: false,
curIndex: 0,
hospitalType: {
val: "类型"
},
pageSize: 15,
pageNum: 1,
filterParams: {}, // 条件
search: '',
areaList: [], // 区域列表
areaShow: ['全部'], // 展示的区域名称
areaCode: "", // 展示的区域编码
areaNameList: [], // 区域名称列表
showPopupPicker: false, // 是否展示区域列表
}
},
beforeRouteEnter(to, from, next) {
if (from.name == 'visit') {
localStorage.setItem('detailsUrl', 'hospitalInfo')
}
if(from.name == 'home'){
localStorage.removeItem('hospitaltype')
}
next();
},
beforeRouteLeave(to, from, next) {
if (to.name == 'visit') {
localStorage.removeItem('detailsUrl')
}
next();
},
mounted() {

if (this.$route.params.type) {
localStorage.setItem('hospitaltype', this.$route.params.type)
}
if(this.$route.params.hospitalbm){
localStorage.setItem('hospitalbm', this.$route.params.hospitalbm)
}
if (window.localStorage.detailsUrl) {
this.detailsUrl = window.localStorage.detailsUrl;
}


this.qryListHospitalArea();
},
methods: {

qryListHospitalArea() {
this.$ajax.post(this.$root.host, {
"MSH.1": "hospitalregistration",
"MSH.2": "listHospitalArea"
}).then(data => {
if (typeof data != 'undefined' && data.length > 0) {
this.areaList = [];
this.areaList = data;
this.areaShow[0] = data[0].MC;
this.areaCode = data[0].BM;
var arr = [];
for (var index = 0; index < data.length; index++) {
const element = data[index];
arr.push(element.MC);
}
this.areaNameList = [];
this.areaNameList.push(arr);
}
this.pageNum = 1;
this.init();
}).catch(error => {
console.log(error);
});
},
valueChange(type) {
if (type) {
this.list = [];
for (var index = 0; index < this.areaList.length; index++) {
const element = this.areaList[index];
if (element.MC == this.areaShow[0]) {
this.areaCode = element.BM;
break;
}
}
this.pageNum = 1;
this.init();
}
},
init(flag, params) {
let baseParams = {
'MSH.1': 'hospitalregistration',
'MSH.2': 'listHospital',
"area": this.areaCode,
'pageNumber': this.pageNum,
'pageSize': this.pageSize,
'ksmc': window.localStorage.hospitaltype ? window.localStorage.hospitaltype : ''
}
if (params) {
this.filterParams = Object.assign(this.filterParams, baseParams, params);
} else {
this.filterParams = Object.assign(this.filterParams, baseParams);
}
this.$ajax.post(this.$root.host, this.filterParams).then(res => {
this.$vux.loading.hide();
if (flag) {
this.$refs.scroll.finishPullUp();
if (typeof res.rows != 'undefined' && res.rows.length > 0) {
this.list = this.list.concat(res.rows);
} else {
this.$vux.toast.text("没有更多了", "bottom");
}
} else {
if (typeof res.rows != 'undefined' && res.rows.length > 0) {
this.list = this.list.concat(res.rows);
}
}
if (this.list.length == 0) {
this.noData = true;
} else {
this.noData = false;
}
}).catch(error => {
console.log(error);
})
},
getDJ(str) {
switch (str) {
case '1':
return '特等'
break;
case '2':
return '甲等'
break;
case '3':
return '乙等'
break;
case '4':
return '合格'
break;
case '5':
return '未定'
break;
default:
return str;
break;
}
},
getSB(str) {
switch (str) {
case '1':
return '社保'
break;
default:
return str;
break;
}
},
pullingUp() {
this.pageNum++;
this.pageSize = 15;
this.init(true);
},
selectChange1(val) {

this.$emit('on-change1', val);
this.hospitalType = val;
let params;
switch (val.key) {
case '1': // 三甲
params = {
level: '2',

}
break;
case '2': // 专科
params = {
level: '5',
}
break;
case '3': // 综合
params = {
level: '1',
}
break;
case '4':
this.filterParams = {};
break;
default:

break;
}
this.$vux.loading.show({
text: '加载中...'
})
this.list = [];
this.pageNum = 1;
this.init(false, params)
},
toSelect1() {
this.showSelect1 = !this.showSelect1;
},
searchHos() {
let params = {
name: this.search,
}
this.list = [];
this.pageNum = 1;
this.init(false, params)
},
todeails(item) {
if (this.$route.params.type || window.localStorage.hospitaltype) {
let ksbm='';
if(this.$route.params.hospitalbm){
ksbm=this.$route.params.hospitalbm;
}else{
ksbm=window.localStorage.hospitalbm;
}
this.$router.push({
name: 'doctorList',
params: {
ksbm: ksbm,
type: '0',
cbm: item.CBM
}
})
} else {
this.$router.push({
name: this.detailsUrl,
params: {
details: item
}
})
}
}
}
}



